import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HeadersComponent } from './components/headers/headers.component';
import { LoginComponent } from './layout/user/login/login.component';
import { ProductModule } from './layout/product/product.module';
import { UserModule } from './layout/user/user.module';
import { UserHomeComponent } from './user/dashborad/user-home/user-home.component';
import { ForgotPasswordComponent } from './exception/forgot-password/forgot-password.component';
const routes: Routes = [
  {path: '', component: UserHomeComponent },
  { path: 'product', loadChildren: './layout/product/product.module#ProductModule' },
  { path: 'user', loadChildren: './layout/user/user.module#UserModule' },
  { path: 'forgot', component: ForgotPasswordComponent },

];

@NgModule({
  imports: [RouterModule.forRoot(routes),
  UserModule,
  ProductModule],
  exports: [RouterModule]
})
export class AppRoutingModule { }
