import {Role} from './enums/Role';

export class User {

    email: string;

    password: string;

    name: string;

    phone: string;

    homeAddress: string;

    street: string;

    city: string;

    province: string;

    zip: string;

    country: string;

    active: boolean;

    role: string;

    constructor() {
        this.active = true;
        this.role = Role.Customer;
    }
}
